#app-header{
  background-color: var(--main-color);
  margin: 0;
  display: flex;
  justify-content: space-between;
  color: white;
}

#app-title {
  margin: 0;
  line-height: 50px;
  margin-left: 25px;
}

#navbar-list {
  list-style-type: none;
  margin: 0;
  margin-right: 25px;
  padding: 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 15px 0;
  font-weight: bold;
}

.navbar-button{
  margin: auto 5px;
  cursor: pointer;
}

#navbar-user-info {
  position: relative;
}

#navbar-user-info > div{
  font-family: Arial, Helvetica, sans-serif;
  padding: 20px;
  display: none;
  position: absolute;
  right: 0;
  background-color: white;
  color: black;
  z-index: 15;
  border-radius: 5px;
  text-align: left;
  font-weight: 600;
  box-shadow: transparent;
}

#navbar-user-info > div > span{
  display: block;
}

#navbar-user-info:hover > div{
  display: block;
}

.navbar-button:hover{
  text-decoration: underline;
}

/* FORMS */

#auth-form-container{
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: rgba(128, 128, 128, 0.568);
  z-index: 14;
}

#auth-form {
  color: black;
  width: 600px;
  height: 300px;
  position: fixed;
  background-color: rgb(67, 69, 71);
  left: 50%;
  top: 65px;
  transform: translateX(-50%);
  z-index: 15;
  max-width: 90%;
  -webkit-animation-name: form-appear;
  animation-name: form-appear;
  -webkit-animation-duration: 0.35s;
  animation-duration: 0.35s;
}

#auth-form-title {
  background-color: rgb(38, 38, 49);
  color: white;
  padding: 10px;
}

#close-auth-form {
  background-color: rgb(123, 123, 226);
  color: white;
  position: absolute;
  right: 0;
  padding: 7.5px;
  margin: 7.5px;
  border-radius: 5px;
  cursor: pointer;
}

#close-auth-form:hover {
  background-color: rgb(228, 2, 2);
}

.auth-form-input {
  display: block;
  margin: 10px auto;
  height: 25px;
  min-width: 200px;
}

#auth-form-button {
  display: block;
  margin: 0 auto;
  cursor: pointer;
}

#auth-form-button:hover {
  background-color:  #017ce0;
}

#auth-form-button:active {
  transform: scale(0.85);
}

@-webkit-keyframes form-appear {
  0%{
    transform: translate(-50%,300px);
  }

  100%{
    transform: translate(-50%, 0)
  }
}

@keyframes form-appear {
  0%{
    transform: translate(-50%,300px);
  }

  100%{
    transform: translate(-50%, 0)
  }
}
