  border: none;
  cursor: pointer;
  padding: 7.5px;
  border-radius: 50%;
  position: absolute;
  right: 50px;
  background-color: rgb(245, 245, 245);
  outline: none;
}

.open-notes-btn:hover {
  background-color: rgb(240, 240, 240);
}

.notes-content {
  min-height: 25px;
  white-space: pre-wrap;
}

.todo-notes-input {
  resize: none;
  display: block;
  margin-left: 20px;
  outline: none;
  background-color: rgb(243, 243, 243);
  border: none;
  min-width: 50%;
  min-height: 50px;
}

.todo-notes-input:focus {
  outline: 1px solid black;
}

.delete-todo-btn {
  background-color: red;
  color: white;
  border: none;
  cursor: pointer;
  padding: 7.5px;
  border-radius: 5px;
  position: absolute;
  right: 0;
}

[class*=checkbox], .delete-todo-btn, .todo-title{
  display: block;
  margin: 0 10px
}

.todo-title{
  text-align: left;
}

.daily-mark{
  margin-left: 5px;
  font-size: 12px;
}


#todo-form {
  padding: 5px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 10px auto;
  font-weight: bold;
  min-width: 300px;
  width: 80%;
  font-size: 15px;
  position: relative;
}

#todo-form > button {
  cursor: pointer;
  margin: 0 5px;
  font-size: 15px;
  border-radius: 5px;
}

#daily-form-btn, #important-form-btn {
  border: none;
  background-color: white;
  width: 35px;
  height: 35px;
  border: 5px black solid;
  position: relative;
}

#daily-form-btn:hover::after, #important-form-btn:hover::after {
  position: absolute;
  bottom: -50px;
  left: 0;
  font-weight: normal;
  width: 120px;
  height: 40px;
  font-size: 15px;
  line-height: 40px;
  background-color: var(--highlight);
  font-family: Arial, Helvetica, sans-serif !important;
}

#daily-form-btn:hover::after {
  content: "Mark Daily";
}

#important-form-btn:hover::after {
  content: "Mark Important";
}

#title-input-container {
  width: 50%;
  display: flex;
  justify-content: space-between;
  margin-right: 5px;
}

#title-input-container > input {
  padding: 5px;
  width: 95%;
}

#todo-submit {
  background-color: var(--main-color);
  border: none;
  padding: 0 25px;
  font-weight: bold;
  color: white;
}

#todo-submit:hover {
  background-color: #017ce0;
}

#todo-submit:active {
  transform: scale(0.85);
}

@media(max-width: 500px){
  #title-input-container {
    width: 65%;
  }

  #todo-form {
    justify-content: space-between;
  }

  #todo-submit {
    width: 100%;
    padding: 5px;
    margin: 0 !important;
    margin-top: 10px !important;
  }

  #daily-form-btn:hover::after, #important-form-btn:hover::after {
    content: "";
    width: 0;
  }
}

#todo-tools {
  width: 100%;
  border-right: 3px solid var(--main-color);
  /* height: 100%;
    overflow: scroll; */
  transition-property: display, width, background-color;
  transition-duration: 0.5s;
  box-sizing: border-box;
}

.todo-tool {
  border-bottom: 1px solid rgba(128, 128, 128, 0.25);
  cursor: pointer;
  padding: 5px 5px 5px 10%;
  font-weight: bold;
  text-align: left;
  position: relative;
}

.filter {
  background-color: var(--highlight);
}

.extra-tool {
  position: absolute;
  display: block;
  padding: 3px;
  right: 5px;
  border: 1px solid  rgba(128, 128, 128, 0.5);
  background-color: white;
  border-radius: 5px;
  top: 50%;
  transform: translateY(-50%);
}

.extra-tool:hover {
  background-color:  rgb(230, 230, 230);
}

.todo-tool:hover {
  background-color: var(--highlight);
}

#tools-toggle {
  display: none;
  margin: 0 auto !important;
  border: none;
  background-color: white;
  cursor: pointer;
  padding: 7.5px;
  border-radius: 5px;
  font-size: 17.5px;
  outline: none;
  box-shadow: 1px 1px 1px rgba(128, 128, 128, 0.267);
}

#tools-toggle:hover {
  background-color: var(--highlight);
}

@media(max-width: 800px){
  .todo-tool {
    display: none;
  }

  #tools-toggle {
    display: block;
  }

  .toggle-visible .todo-tool{
    display: block !important;
    clear: left;
  }

  .toggle-visible #tools-toggle {
    float: left;
    margin-left: 2.5px !important;
    box-shadow: none;
  }

  .toggle-visible{
    grid-area: unset;
    width: 200px !important;
    z-index: 5;
    background-color: rgb(245, 245, 245) !important;
    box-shadow: 2px 2px 3px rgba(128, 128, 128, 0.438);
  }
}

#item-container {
  display: grid;
  grid-template-columns: 1fr 4fr;
  grid-template-areas: 
        'form form'
        'tools list';
  max-width: 100vw;
}

#todo-form {
  grid-area: form;
}

#todo-tools {
  grid-area: tools;
}

#todo-list {
  grid-area: list;
}

@media(max-width: 800px){
  #todo-container {
    grid-template-columns: 35px 4fr;
  }
}
